@import "mixins";

/* ------------ About Page ---------- */
.type-about .main {
  // quote
  .quote-wrapper.full-width-bar-inner {
    display: flex;
    margin-top: 50px;
    @media screen and (max-width: 1224px) {
      padding-right: 35px;
    }
    @media screen and (max-width: 767px) {

      padding: 0px 25px;

      .h9 {
        text-align: left;
        line-height: 24px;
        font-weight: normal;
        //color: $medgray;
      }


    }
  }
  // switch to background for mobile/tablet
  @media screen and (max-width: 1023px) {
    .quote-wrapper.full-width-bar-inner:before {
      content: 'â€œ';
      font-weight: bold;
      font-size: 200px;
      font-family: $vitesse;
      color: $lightgray;
      margin-top: 30px;
      margin-right: -90px;
      padding: 0px 25px;
      @media screen and (max-width: 767px) {
        margin-left: -45px;
      }
    }
  }
  .quote-icon {
    font-weight: bold;
    font-size: 200px;
    font-family: $vitesse;
    color: $lightgray;
    margin-top: 30px;
    // come back to this for smaller sizes/bg instead/overlap
    @media screen and (max-width: 1023px) {
      display: none;

    }
  }
  /// quote source mobile
  .field--name-field-quote-source {
    max-width: 430px!important;

    @media screen and (max-width: 767px) {
      //color: $medgray;
      padding: 0px 25px;
    }
  }

  // Links
  .full-width-bar.links-video-group {
    margin-top: 30px;
    padding: 40px 25px 60px;
    @media screen and (max-width: 767px) {
      padding: 0px 0px 40px;
    }
    &.bg-lightgray {
      @media screen and (max-width: 1223px) {
        background: none;
        margin-top: 0px;
      }
    }

    iframe {
      @media screen and (max-width: 767px) {
        max-width: 100%;
      }
      @media screen and (min-width: 768px) and (max-width: 1023px) {
        //width: 766px;
        //height: 405px;
        width: 100%;
      }
      @media screen and (min-width: 1024px) {
        //width: 766px;
        //height: 405px;

        width: 100%;
        //height: -webkit-fill-available;
        max-height: 600px;
      }
    }
  }
  .about-video-links-group {
    flex-wrap: wrap;
    @media screen and (max-width: 767px) {

    }
  }

  .field--name-field-links {
    width: 30%;
    //margin-top: -20px; // moving up to have links exactly aligned w video top
    margin-left: 0px;
    margin-right: 0px;
    @media screen and (max-width: 1223px) {
      //width: 100%;
      padding: 0px 25px;
      order: 2;
    }

    @media screen and (min-width: 1224px) and (max-width: 1824px) {
      width: 30%;
    }
    @media screen and (min-width: 1024px) and (max-width: 1223px) {
      //order: 2;
      width: 60%;
      display: inline-block; // to get instagram block up on same line
      margin-left: 0px!important;
      margin-top: 30px;
      column-count: 2;
      background: $lightgray;
      padding: 40px 25px;
    }
    @media screen and (min-width: 768px) and (max-width: 1023px) {
      width: 50%;
      margin-left: 0px!important;
      margin-top: 10px;
      display: inline-block; // to get instagram block up on same line
      //order: 2;
      background: $lightgray;
      padding: 40px 25px;
    }
    @media screen and (max-width: 767px) {
      width: 100%;
      //order: 2;
      margin-top: 0px;
      margin-top: 10px;
      background: $lightgray;
      padding: 40px 25px;
    }
  }
  // video field
  .field--name-field-video {
    width: 70%;
    margin-top: 20px;
    margin-left: 0px;
    margin-right: 0px;
    @media screen and (min-width: 768px) and (max-width: 1223px) {
      width: 100%;
      order: 1;

    }
    @media screen and (max-width: 767px) {
      width: 100%;
      order: 1;
      margin-top: 0px;
    }
  }

  // Instagram block
  .field--name-field-block {
    width: 70%; // needs to mimick field-video to keep alignment
    margin-left: auto;
    margin-right: 0px!important;
    height: 235px; // for large, 1 row of 3 sections
    background: gray; // temp for positioning
    @media screen and (min-width: 1024px) and (max-width: 1223px) {
      height: 480px; // 235px + 235px + 10px - 2 rows
      order: 3;
      width: 40%;
      margin-top: 30px;
    }
    @media screen and (min-width: 768px) and (max-width: 1023px) {
      width: 48%;
      order: 3;
      height: 725px; // may change to auto when you put in instagram
      margin-top: 10px;
    }

    @media screen and (max-width: 767px) {
      order: 3;
      width: 480px;
      max-width: 100%;
      height: 725px; // 3 rows w spacing between  // may change to auto when you put in instagram
      margin-right: auto!important;
    }
  }




  //
  //.field--name-field-links a:hover, .field--name-field-links a:active {
  //  color: $royal;
  //  text-decoration: none;
  //}

  //yfacts bar
  .full-width-bar.yfacts-about {
    height: 400px;
    padding-top: 250px;

    font-family: $gotham;
    font-style: italic;
    font-size: 22px;
    line-height: 28px;
    margin: 0px 0px 60px 0px;
    p {
      color: $xlightgray;
    }
    .larger-p, .larger-p2 {
      white-space: nowrap;
      overflow: hidden;
      //width: 30em;
      line-height: 30px;
      width: 100%;
      display: none;
      @media screen and (min-width: 768px) {
        //animation: type 4s steps(70, end);
      }
    }
    .larger-p2 {
      @media screen and (min-width: 768px) {
        //animation: type2 4s steps(50, end);
      }
    }
    .larger-text {
      font-size: 36px;
      color: #fff;
      font-weight: 500;
    }
  }
  // change to trigger on scroll to bar

  // students facts
  .students-group {
    .barWrapper {
      padding: 0px;
    }
    .tooltip {
      position: relative;
      float: right;
      background-color: none;
      opacity: 1 !important;
    }
    .tooltip > .tooltip-inner {
      background: none;
      padding: 15px 15px;
      @include h11();
      padding-right: 35px !important; // strangely needed to center number now.. wasn't needed before
      @media screen and (max-width: 767px) {
        top: -39px;
        position: relative;
      }
      @media screen and (max-width: 1023px) {
        padding-right: 35px !important;
        left: 10px;
      }
      @media screen and (min-width: 1024px) {
        padding-right: 25px !important;
      }
    }
    .color-2 .tooltip > .tooltip-inner {
      padding-right: 25px !important; // strangely needed to center number now.. wasn't needed before

    }
    .popOver + .tooltip > .tooltip-arrow {
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-top: 5px solid #eebf3f; // katria come back to
      @media screen and (min-width: 1024px) {
        //margin-left: 35px!important;
      }
    }

    .assets-wrap {
      opacity: 1;
    }
    .tooltip.bottom .tooltip-arrow {
      margin-left: -10px;
      //margin-left: 10px;
      border-width: 0px 10px 20px;
      border-left: 10px solid $navy;
      border-right: 10px solid $navy;
      border-color: $navy;
      opacity: 1;
      border-radius: 36px;
      margin-top: -9px;
      @media screen and (max-width: 1023px) {
        margin-left: -20px;
      }
    }
    .color-2 .tooltip.bottom .tooltip-arrow {
      border-color: $medgray;
      @media screen and (min-width: 1024px) {
        //margin-left: 23px!important;
      }

    }
    .progress {
      border-radius: 0;
      overflow: visible;
      height: 8px;
      background-color: #e6e6e6; // katria come back to
      box-shadow: none;
      @media screen and (max-width: 767px) {
        width: 70%;
        background: none;
      }
    }
    .progress-bar {
      background: rgb(0, 46, 93);
      -webkit-transition: width 1.5s ease-in-out;
      transition: width 1.5s ease-in-out;

    }
    .progress-bar.color-2 {
      background: $medgray;
    }

  }
  // student group layout
  $totalStudentsHeight: 170px;
  $totalStudentsWidth: 23%;
  $totalStudentsHeightMobile: 100px;
  .students-group.row {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 75px;
    @media screen and (min-width: 768px) and (max-width: 1023px) {
      padding: 0px 35px;
    }
    @media screen and (max-width: 767px) {
      flex-direction: column;
      margin-bottom: 40px;
    }
  }
  .grad-wrapper {
    order: 3;
    margin-top: -($totalStudentsHeight / 2);
    @media screen and (max-width: 767px) {
      order: 2;
      margin-top: 0px;
    }
  }

  .field--name-field-undergrads,
  .field--name-field-grads {
    width: 100% - $totalStudentsWidth;
    @media screen and (max-width: 767px) {
      width: 100%;
      padding: 0px 35px!important;
    }
  }

  // total students


  .field--name-field-total-students .field__label {
    line-height: 20px;
    text-align: right;
    @media screen and (max-width: 767px) {
      text-align: left;
      display: inline;
    }
  }
  .field--name-field-total-students {
    width: $totalStudentsWidth;
    background: $xlightgray;
    display: inline;
    height: $totalStudentsHeight;
    padding: 25px 45px;

    @media screen and (max-width: 767px) {
      height: $totalStudentsHeightMobile;
      width: 100%;
      order: 3;
      padding: 25px 35px;
    }
    @media screen and (min-width: 768px) and (max-width: 1024px) {
      padding: 25px 25px 25px 20px;
    }
    .field__item {
      font-size: 55px;
      color: $navy;
      font-weight: bold;
      text-align: right;
      padding: 25px 0px 0px;


      @media screen and (max-width: 767px) {
        display: block;
        text-align: left;
        padding-top: 5px;
      }
      @media screen and (max-width: 1224px) {
        font-size: 30px;

      }
    }
  }

