  // ------ Gender Bars
  $genderBarHeight: 50px;
  .field--name-field-gender {
    display: flex;
    margin-bottom: 80px;
    @media screen and (max-width:1023px) {
      padding: 0px 25px;
    }
    .bar2 {
      text-align: end;
    }
    .bar2 .progress {
      -moz-transform: scale(-1, 1);
      -webkit-transform: scale(-1, 1);
      -o-transform: scale(-1, 1);
      -ms-transform: scale(-1, 1);
      transform: scale(-1, 1);
    }
    .tooltip {
      position: relative;
      float: right;
      background-color: unset;
      /*   opacity: 1!important; */
    }
    .tooltip > .tooltip-inner {
      background: none;
      padding: 15px 15px;
      color: rgb(23, 44, 66);
      font-weight: bold;
      font-size: 13px;
    }
    .popOver + .tooltip > .tooltip-arrow {
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-top: 5px solid #eebf3f;
    }

    .progress {
      border-radius: 0;
      overflow: visible;
      height: $genderBarHeight;
      box-shadow: none;
      background: none;
      //z-index: 1;
    }
    .bar2 .progress {
      //z-index: 0;
    }
    .progress-bar {
      background: $teal;
      -webkit-transition: width 1.5s ease-in-out;
      transition: width 1.5s ease-in-out;
      border-radius: 0px;
      height: $genderBarHeight;
      border: none;
    }
    .progress-bar.color-2 {
      background: $lightteal;
      z-index: 1;
    }
    .progressText {
      // smaller screens just have it show above
      @media screen and (min-width: 1224px) {
        display: none;
        margin-top: 1px;
        margin-bottom: -($genderBarHeight+3);
        padding: 0px 15px;
        z-index: 100;
        position: relative;
      }

    }
    .progressText2.h9 {
      padding-left: 3px;
    }
  }

  // ----- Demographics & map area
  $mapWidth: 816px;
  $innerMapTopContentWidth: $mapWidth - 60px; // for states & countries etc
  $innerMapBottomContentWidth: 600px;

  $largeLeftMapContentMargin: 80px; // top left content w states info - 1024px+
  h2.demographics-main-top-label.left-text {
    @media screen and (max-width: 1023px) {
      margin-bottom: -70px;
      text-align: center;
    }
    @media screen and (max-width: 767px) {
      margin-bottom: -60px;
    }
  }

