  .demographics1-large-wrapper {
    //  max-width: 800px;
    @media screen and (min-width: 1024px) {
      margin-left: $largeLeftMapContentMargin;
    }
  }
  // states & countries
  .demographics1 {
    display: flex;
    //justify-content: flex-start;
    //width: $innerMapContentWidth;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    @media screen and (max-width: 1023px) {
      text-align: center;
      width: 500px;
    }
    @media screen and (max-width: 767px) {
      padding: 0px 25px;
      flex-direction: column;
      margin-bottom: 0px;
    }
  }


  .demographics1-top-label {
    //width: $innerMapContentWidth;
    margin-left: auto;
    margin-right: auto;
    @media screen and (max-width: 1023px) {
      padding-left: 25px;
      padding-top: 40px;
      width: 500px;
    }
    //padding-left: 19px;
  }
  .states-cont {
    width: 188px;
    @media screen and (min-width: 1024px) {
      width: 200px;
    }

    height: 40px;
    // adjust for mobile/responsive
    @media screen and (max-width: 767px) {
      margin-left: -10px;
    }
  }
  .states-cont, .countries-cont {
    display: flex;
    //justify-content: space-between; // breaks typing
  }
  .state-field-wrapper {
    height: 40px;
    width: 55px;
  }
  .countries-field-wrapper {
    height: 40px;
    width: 70px;
  }
  .field--name-field-states, .field--name-field-countries,
  .states-label, .countries-label {
    white-space: nowrap;
    overflow: hidden;
    display: none;
    //width: 100%; // is a test
    @media screen and (max-width: 1023px) {
      display: block!important;
    }

  }

  .states-label, .countries-label {
    text-transform: uppercase;
    //@media screen and (max-width: 1023px) {
    //  display: block;
    //}
  }

  .states-cont, .countries-cont {
    display: flex;
  }
  .countries-cont {
    @media screen and (min-width: 1024px) {
      width: 300px;
    }
    @media screen and (max-width: 767px) {
      position: relative;
      top: 285px; //$mobileUSHeight + 30px;
      margin-bottom: 0px;
    }
  }

  .states-label, .countries-label {
    white-space: nowrap;
    overflow: hidden;
    display: none;
    //margin-left: 10px;
    //width: 100%; // is a test
  }

  .and-icon {

    display: none;
    @media screen and (min-width: 768px) and (max-width: 1023px) {
      display: block;
    }
    @media screen and (max-width: 767px) {
      display: none!important;
    }

    font-weight: 500;
    font-size: 130px;
    font-family: $gotham;
    color: $navy;
    padding: 0px 25px;
    @media screen and (max-width: 1023px) {
      padding-top: 30px;
    }
  }

  // state percentages
  .state-percents-group {
    max-width: 285px;
    width: 285px;
    height: 64px;
    @media screen and (max-width: 767px) {
      width: 100%;
      max-width: 100%;
      margin-top: -40px;
      margin-bottom: -30px;
      //padding-right: 40%;
      padding-right: 35px;
      padding-left: 25px;
      max-width: 320px;
    }

    @media screen and (min-width: 768px) and (max-width: 1023px) {
      margin-left: auto;
      margin-right: auto;
      width: 550px;
      max-width: 550px;
      margin-top: -30px;
      padding-right: 315px;
    }
    @media screen and (min-width: 1024px) {
      margin-left: $largeLeftMapContentMargin;
    }
    //height: 164px;
    //background: url('http://localhost.byu.edu:7070/sites/default/files/images/US-map-opt.svg') no-repeat;
    //padding-top: 107px;
    //background-position-y: 12px;
    //background-size: 139px;
    //background-position-x: 47px;


    .field.h12 {
      text-transform: none;
      //display: inline;
      display: none;
      @media screen and (max-width: 1023px) {
          font-size: 14px;
      }
    }
    .field.h12:after {
      content: "\2022";
      color: $navy;
      font-size: 20px;
      padding-left: 3px;
      position: relative;
      top: 2px;
    }
    // arizona block
    .h12.field.field--name-field-percent-arizona {

      //display: block;
      display: none;
    }

    & > .state-2 {
      animation-delay: 300ms
    }
    & > .state-3 {
      animation-delay: 1600ms
    }
    & > .state-4 {
      animation-delay: 2900ms
    }
    & > .state-5 {
      animation-delay: 3200ms
    }
  }
  // US map
  $largeUSWidth: 130px;
  $largeUSHeight: 100px;
  $tabletUSWidth: 360px;
  $tabletWorldWidth: 450px;
  $tabletUSHeight: 255px;
  $mobileUSWidth: 300px;
  $mobileUSHeight: 255px;
  svg.us-dots-1 {
    width: $largeUSWidth;
    height: $largeUSHeight;

  }

  .us-map-wrapper {
    width: $largeUSWidth;
    float: left;
    margin-top: -22px;
    margin-left: 52px;
    @media screen and (max-width: 767px) {
      width: 100%;
      margin: 0px;
    }
    @media screen and (min-width: 768px) and (max-width: 1023px) {
      width: 48%;
      margin: 0px;
      text-align: center;

    }
  }
  .world-map-mobile-wrapper {

    width: $largeUSWidth;
    float: right;
    width: 50%;
    margin: 0px 0px 0px -3%;
    text-align: center;
    @media screen and (max-width: 767px) {
      width: 100%;
    }
    @media screen and (min-width: 1024px) {
      display: none;
    }
  }
  #dots1, #dots2, #dots3, #dots4, #dots5, #dots6, #dots7 {
    display: none;
  }
  //tablet us map
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    svg.us-dots-1, #world-map-mobile {
      width: $tabletUSWidth;
      height: $tabletUSHeight;
      margin-top: -25px;
    }
    #world-map-mobile {
      width: $tabletWorldWidth;
    }
  }
  // mobile us map
  @media screen and (max-width: 767px) {
    svg.us-dots-1, #world-map-mobile {
      width: $mobileUSWidth;
      max-width: 100%;
      height: $mobileUSHeight;
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  }

  //  display: block;
  //}

  // languages
  .demographics3 {
    @media screen and (min-width: 1024px) {
      margin-top: -100px;
    }
    @media screen and (max-width: 1023px) {
      background: #fff;
      margin-top: 250px;
      padding: 30px 15px;
      width: 100%;
      padding-left: 50%;
      display: flex;
      justify-content: center;
    }
    @media screen and (max-width: 767px) {
      padding: 0px 35px 40px;
      background: none;
      margin-top: 0px;
    }


    .navy-circle {
      @include navy-circle(156px);
      @media screen and (max-width: 1023px) {
        margin-left: 0px;
        margin-top: 0px;
        margin: 0px 5px;

      }
    }
  }

  // circles
  $about-circle-width: 156px;
  $circle-left-movement: 490px;
  $between-circles-adjustment: 130px;

