    width: 416px;
    height: 72px;
    @media screen and (max-width: 1023px) {
      display: none;
    }
  }

  .languages-taught-circle {
    //position: relative;
    margin-left: $langTaughtMargin;
    visibility: hidden;
    @media screen and (max-width: 1023px) {
      visibility: visible;
    }
  }
