  .languages-taught-text {
    line-height: 17px;
    text-align: center;
  }
  .languages-spoken-circle {
    //position: relative;
    margin-left: $studentsSpeakingBarWidth - ($about-circle-width / 2);
    margin-top: -80px;
    visibility: hidden;
    @media screen and (max-width: 1023px) {
      visibility: visible;
    }
  }

  .field--name-field-languages-spoken {
    line-height: 55px;
  }
  .languages-spoken-text {
    line-height: 17px;
    text-align: center;
  }
  .vertical-dots.after-spoken {
    //position: relative;
    margin-left: $studentsSpeakingBarWidth - 8px; // back 8 for width of small dots
    //width: 416px;
    height: 72px;
    @media screen and (max-width: 1023px) {
      display: none;
    }
  }

  // students speaking & certifications
  .demographics4 {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: -120px;
    @media screen and (max-width: 1023px) {
      margin-top: -180px;
      height: 330px;
      background: #fff;
      width: 100%;
      flex-direction: row;
      flex-wrap: wrap;
      padding-right: 50%;
      justify-content: flex-start;
      align-content: flex-start;
      padding-left: 7%;
    }
    @media screen and (max-width: 767px) {
      margin-top: 0px;
      padding: 0px 35px;
      background: none;
    }
  }

  // --- certifications section
  // --- certifications section
  .certifications-group-wrapper {
    display: none;
    //visibility: hidden;
    @media screen and (max-width: 1023px) {
      display: block;
    }
    width: 290px;
    height: 218px;
  }

  .award-icon-wrapper {
    width: 290px;
  }
  .award-icon {
    width: 13%; // 7 in a row
    padding: 4px 0px;
  }
  //----- students speaking
  .field--name-field-students-speaking {
    //display: none;
    .students-speaking-text-wrapper {
      //display: none; // displayed w animation
      visibility: hidden;
      width: $studentsSpeakingBarWidth;
      line-height: 18px; // necessary to override Aleni's default big spacing
      padding-bottom: 10px;
      .h9 {
        line-height: 22px;
      }
      @media screen and (max-width: 1023px) {
        visibility: visible;
      }
    }

  }

  .barWrapper.students-speaking {
    //display: flex;
    display: none; // displayed w animation
    margin-bottom: 0px;
    width: $studentsSpeakingBarWidth;

    .progress {
      border-radius: 0;
      overflow: visible;
      height: $studentsSpeakingBarHeight;
      width: $studentsSpeakingBarWidth;
      box-shadow: none;
      background: $lightgray;
      //z-index: 1;
    }
    .progress-bar {
      background: $medblue;
      -webkit-transition: width 1.5s ease-in-out;
      transition: width 1.5s ease-in-out;
      border-radius: 0px;
      height: $studentsSpeakingBarHeight;
      border: none;
    }

  }

  // more link
  .field--name-field-more-facts {
    width: 250px;
    margin-top: 60px;
    margin-bottom: 60px;
    @media screen and (min-width: 768px) and (max-width: 1023px) {
        width: 250px;
        margin-top: -140px;
        margin-bottom: 0px;
        margin-left: 62%;
    }
    @media screen and (max-width: 767px) {
      margin: 0px auto 20px auto;

    }
  }

